import React, {useEffect, useState} from "react";
import {
    Button,
    Card,
    CardBody,
    CardGroup,
    CardSubtitle,
    CardTitle,
    Col, Form, FormGroup, FormText, Input, Label,
    ListGroup,
    ListGroupItem,
    Row,
    Table
} from "reactstrap";
import {Link, useLocation, useNavigate, useParams} from "react-router-dom";
import 'react-gallery-carousel/dist/index.css';
import {useDispatch, useSelector} from "react-redux";
import MySideBar from "../components/MySideBar";
import $ from "jquery";
import {clearAccessToken, setAccessToken, setUser} from "../createReduxStore";
import {userService} from "../services/user.service";

const ProductList = (props) => {
    const [isShowMore, setIsShowMore] = useState(true);
    const navigate = useNavigate();
    const user = useSelector((state) => state.user);
    const toggleReadMore = () => setIsShowMore((show) => !show);
    const [productData, setProductData] = useState(null);
    const [images, setImages] = useState([]);
    const {id} = useParams();
    const dispatch = useDispatch();

    const location = useLocation()
    const {search} = useLocation();
    let query = new URLSearchParams(search);

    function numberWithCommas(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    React.useEffect(() => {
        if (!user) navigate('/login');
    }, []);

    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, [])

    function handleSubmit(event) {
        let params = new URLSearchParams($('#profileForm').serialize());
        userService.updateInfo(params).then(res => {
            // setApiResponse(res.data);
            alert("Cập nhật thông tin cá nhân thành công!");
            dispatch(setUser({user:res.data}));
        }).catch(error => {
            console.log(error);
            alert(JSON.stringify(error.response.data));
            dispatch(clearAccessToken());
        }).finally(() => {
            // setIsLoading(false);
        });
        event.preventDefault();
    }

    return (
        <div>
            {!!user &&
                <Row className={'g-2'}>
                    <Col md='4'>
                        <MySideBar activePage={'userProfile'}/>
                    </Col>
                    <Col md='8'>
                        <Row>
                            <Col>
                                <Card>
                                    <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                                        <i className="bi bi-person-bounding-box me-2"></i>
                                        Chỉnh sửa thông tin cá nhân
                                    </CardTitle>
                                    <CardBody>
                                        <Form id={'profileForm'} onSubmit={handleSubmit}>
                                            <FormGroup>
                                                <Label for="username">Username</Label>
                                                <Input
                                                    disabled
                                                    id="username"
                                                    name="username"
                                                    placeholder="Nhập tên đăng nhập"
                                                    type="text"
                                                    value={user.username}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="email">Email</Label>
                                                <Input
                                                    required
                                                    id="email"
                                                    name="email"
                                                    placeholder="Nhập email"
                                                    type="email"
                                                    defaultValue={user.email}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="name">Họ và tên</Label>
                                                <Input
                                                    required
                                                    id="name"
                                                    name="name"
                                                    placeholder="Nhập họ và tên"
                                                    type="text"
                                                    defaultValue={user.name}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="address">Địa chỉ</Label>
                                                <Input
                                                    required
                                                    id="address"
                                                    name="address"
                                                    placeholder="Nhập địa chỉ"
                                                    type="text"
                                                    defaultValue={user.address}
                                                />
                                            </FormGroup>
                                            <FormGroup>
                                                <Label for="phone_number">Số điện thoại</Label>
                                                <Input
                                                    required
                                                    id="phone_number"
                                                    name="phone_number"
                                                    placeholder="Nhập địa chỉ"
                                                    type="tel"
                                                    defaultValue={user.phone_number}
                                                />
                                            </FormGroup>
                                            <Button>Submit</Button>
                                        </Form>
                                    </CardBody>
                                </Card>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            }
        </div>
    )
        ;
};

export default ProductList;
