import React, {useEffect, useState} from "react";
import {publicCategoryService, publicProductService} from "../services/public";
import {Button, Card, CardGroup, Col, Row, Spinner} from "reactstrap";
import ProductCard from "../components/ProductCard";
import {useLocation} from "react-router-dom";
import ProductFilter from "../components/ProductFilter";
import Loader from "../layouts/loader/Loader";

const ProductList = (props) => {
    const [productList, setProductList] = useState([]);
    const [isLoading, setIsLoading] = useState([]);
    const [nextPage, setNextPage] = useState([]);
    const [categoryData, setCategoryData] = useState([]);

    const location = useLocation()
    const { search } = useLocation();
    let query = new URLSearchParams(search);

    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, [])

    React.useEffect(() => {
        console.log("router change detect");
        console.log(productList);
        let query = new URLSearchParams(search);
        setProductList([]);
        loadData(query, true);
    }, [location]);

    function updateProductListData(res, isReset=false) {
        let newProductList = productList;
        if (isReset) {
            newProductList = [];
        }
        newProductList.push.apply(newProductList, res.data.results);
        setProductList(newProductList);
        setNextPage(res.data.links.next);
    }

    function loadData(query, isReset=false) {
        setIsLoading(true);
        publicProductService.getProductFilter(query).then(res => {
            updateProductListData(res, isReset);
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error)
        }).finally(() => {
            setIsLoading(false);
        });
    }

    function handleClickMoreAction() {
        console.log("handle click more action");
        setIsLoading(true);
        publicProductService.getProductNextPage(nextPage).then(res => {
            updateProductListData(res);
        }).catch(error => {
            console.log(error)
        }).finally(() => {
            setIsLoading(false);
        })
    }

    return (
        <div>
            <div className="mb-4">
                <ProductFilter data={categoryData} />
            </div>
            <Row className="g-1">
                {productList.map((item) => (
                    <Col className="m-0" sm="6" lg="6" xl="3" key={item.id}>
                        <ProductCard
                            product={item}
                        />
                    </Col>
                ))}
            </Row>
            {isLoading &&
                // <div>
                //     <Row>
                //         <Col sm="6" lg="6" xl="3" key="key1"><ProductListSke/></Col>
                //         <Col sm="6" lg="6" xl="3" key="key2"><ProductListSke/></Col>
                //         <Col sm="6" lg="6" xl="3" key="key3"><ProductListSke/></Col>
                //         <Col sm="6" lg="6" xl="3" key="key4"><ProductListSke/></Col>
                //     </Row>
                // </div>
                <Loader/>
            }
            {!isLoading && !!nextPage &&
                <Row className="mt-3 view-more justify-content-center">
                    <Button className={"w-25"} onClick={handleClickMoreAction}>Xem thêm sản phẩm</Button>
                </Row>
            }
        </div>
    );
};

export default ProductList;
