import { lazy } from "react";
import ProductList from "../views/ProductList";
import ProductDetail from "../views/ProductDetail";
import HomePage from "../views/HomePage";
import Register from "../views/Register";
import Login from "../views/Login";
import UserProfile from "../views/UserProfile";
import OrdersHistory from "../views/OrdersHistory";
import Cart from "../views/Cart";
import Checkout from "../views/Checkout";

/****Layouts*****/
const FullLayout = lazy(() => import("../layouts/FullLayout.js"));

/***** Pages ****/

/*****Routes******/

const ThemeRoutes = [
  {
    path: "/",
    element: <FullLayout />,
    children: [
      { path: "/", exact: true, element: <HomePage /> },
      { path: "/products", exact: true, element: <ProductList /> },
      { path: "/products/:id", exact: true, element: <ProductDetail /> },
      { path: "/login", exact: true, element: <Login /> },
      { path: "/register", exact: true, element: <Register /> },
      { path: "/profile", exact: true, element: <UserProfile /> },
      { path: "/orders", exact: true, element: <OrdersHistory /> },
      { path: "/cart", exact: true, element: <Cart /> },
      { path: "/checkout", exact: true, element: <Checkout /> },
    ],
  },
];

export default ThemeRoutes;
