import React, {useEffect, useState} from "react";
import {publicCategoryService, publicProductService} from "../services/public";
import {Button, Card, CardGroup, CardTitle, Col, Row, UncontrolledCarousel} from "reactstrap";
import ProductCard from "../components/ProductCard";
import Skeleton, {SkeletonTheme} from "react-loading-skeleton";
import ProductListSke from "../components/skeletons/ProductListSke";
import {Link, useLocation} from "react-router-dom";

const HomePage = (props) => {
    const [isLoading, setIsLoading] = useState([]);
    const [categoryData, setCategoryData] = useState([]);

    React.useEffect(() => {
        setIsLoading(true);
        publicProductService.getHomePageData().then(res => {
            setCategoryData(res.data);
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error)
        }).finally(() => {
            setIsLoading(false);
        });
    }, []);

    return (
        <div>
            <UncontrolledCarousel
                captionText=''
                items={[
                    {
                        altText: '',
                        caption: '',
                        key: 1,
                        src: 'https://cdn.tgdd.vn/2021/12/banner/830-300-830x300-24.png'
                    },
                    {
                        altText: '',
                        caption: '',
                        key: 2,
                        src: 'https://cdn.tgdd.vn/2022/01/banner/830-300-830x300-4.png'
                    },
                    {
                        altText: '',
                        caption: '',
                        key: 3,
                        src: 'https://cdn.tgdd.vn/2022/01/banner/830-300-830x300-1.png'
                    }
                ]}
            />
            {categoryData.length > 0 ? categoryData.map((item) => (
                    <Card key={item.id} className={'mt-2'}>
                        <Link to={'/products?category=' + item.id}>
                            <CardTitle className="pt-3 ps-3 font-bold" tag="h5">{item.name}</CardTitle>
                        </Link>
                        <Row className={'g-1'}>
                            {item.products.map((item) => (
                                <Col className="m-0" sm="6" lg="6" xl="3" key={item.id}>
                                    <ProductCard
                                        product={item}
                                    />
                                </Col>
                            ))}
                        </Row>
                    </Card>
                )) :
                <div>
                    <Row>
                        <Col sm="6" lg="6" xl="3" key="key1"><ProductListSke/></Col>
                        <Col sm="6" lg="6" xl="3" key="key2"><ProductListSke/></Col>
                        <Col sm="6" lg="6" xl="3" key="key3"><ProductListSke/></Col>
                        <Col sm="6" lg="6" xl="3" key="key4"><ProductListSke/></Col>
                    </Row>
                </div>
            }
        </div>
    );
};

export default HomePage;
