import axios from 'axios';
import Constants from "../../utils/constants";
import Auth from "../../utils/auth";

export const publicProductService = {
    getAllProduct,
    getProductFilter,
    getProductNextPage,
    getProductDetail,
    getHomePageData,
    getProductSearch,
};


function getHomePageData() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/home_page`
    });
}

function getAllProduct() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/products?page_size=20`
    });
}

function getProductFilter(params) {
    // if (!search) return getAllProduct();
    params.set('page_size', 20);
    console.log("api");
    for (const [key, value] of params) {
        console.log(key, value);
    }
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/products`,
        params: params,
    });
}

function getProductSearch(searchName) {
    // if (!search) return getAllProduct();
    let params = new URLSearchParams();
    params.set('page_size', 20);
    params.set('search_name', searchName);
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/products/lite`,
        params: params,
    });
}

function getProductNextPage(nextPage) {
    return axios({
        method: 'GET',
        url: nextPage,
    });
}

function getProductDetail(id) {
    // if (!search) return getAllProduct();
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/products/${id}`,
    });
}

