import React, {useEffect, useState} from "react";
import {
    Card,
    CardBody,
    CardTitle,
    Col,
    Row,
} from "reactstrap";
import 'react-gallery-carousel/dist/index.css';
import numberWithCommas from "../utils/string";
import {useDispatch, useSelector} from "react-redux";
import MySideBar from "../components/MySideBar";
import {userService} from "../services/user.service";
import {clearAccessToken} from "../createReduxStore";
import Loader from "../layouts/loader/Loader";


const OrdersHistory = (props) => {
    const [orderList, setOrderList] = useState([]);
    const [isLoading, setIsLoading] = useState([]);
    const [nextPage, setNextPage] = useState([]);
    const user = useSelector((state) => state.user);
    const dispatch = useDispatch();

    React.useEffect(() => {
        window.scrollTo(0, 0);
        loadData();
    }, [])

    function updateProductListData(res) {
        let newOrderList = orderList;
        newOrderList.push.apply(newOrderList, res.data.results);
        setOrderList(newOrderList);
        setNextPage(res.data.links.next);
    }

    function loadData() {
        setIsLoading(true);
        userService.getAllOrder().then(res => {
            console.log(res);
            updateProductListData(res);
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
            setIsLoading(false);
        });
    }

    return (
        <div>
            {!!user &&
                <Row className={'g-2'}>
                    <Col md='4'>
                        <MySideBar activePage={'ordersHistory'}/>
                    </Col>
                    <Col md='8'>
                        <Row>
                            <Col>
                                {/* --------------------------------------------------------------------------------*/}
                                {/* Card-1*/}
                                {/* --------------------------------------------------------------------------------*/}
                                <Card>
                                    <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                                        <i className="bi bi-clock-history me-2"></i>
                                        Lịch sử đơn hàng
                                    </CardTitle>
                                    {isLoading &&
                                        <Loader/>
                                    }
                                    {!isLoading && orderList.map((item) => (
                                        <CardBody key={item.id} className={'border-bottom'}>
                                            <Row>
                                                <Col md={'4'}>
                                                    <CardTitle className={'font-bold'}>Mã đơn
                                                        hàng: {item.id}</CardTitle>
                                                    <p className={'ms-2'}>Người nhận: {item.name}</p>
                                                    <p className={'ms-2'}>Địa chỉ: {item.address}</p>
                                                    <p className={'ms-2'}>Số điện thoại: {item.phone_number}</p>
                                                    <p className={'ms-2'}>Trạng thái: {item.status}</p>
                                                    <p className={'ms-2'}>Tổng giá tiền: {numberWithCommas(item.total_cost)} VND</p>
                                                </Col>
                                                <Col md='8'>
                                                    {!isLoading && item.items.map((cartItem) => (
                                                        <Row key={cartItem.id} className={'text-center mt-2'}>
                                                            <Col md={'2'}>
                                                                <div className="imgWrapper"
                                                                     style={{
                                                                         backgroundImage: `url(${cartItem.product.thumbnail})`,
                                                                     }}>
                                                                    {/*<CardImg className="pt-1" alt="Card image cap" src={props.product.thumbnail} />*/}
                                                                </div>
                                                            </Col>
                                                            <Col md='7'>
                                                                <CardTitle>{cartItem.product.name} x {cartItem.count}</CardTitle>
                                                            </Col>
                                                            <Col md='3'>
                                                                <CardTitle>{numberWithCommas(cartItem.order_price * cartItem.count)}</CardTitle>
                                                            </Col>
                                                        </Row>
                                                    ))}
                                                    {/*<Row>*/}
                                                    {/*{item.items.map((orderItem) => (*/}
                                                    {/*<Col className="imgWrapper"*/}
                                                    {/*     style={{*/}
                                                    {/*         backgroundImage: `url(${orderItem.product.thumbnail})`,*/}
                                                    {/*         height: 50,*/}
                                                    {/*     }}>*/}
                                                    {/*    /!*<CardImg className="pt-1" alt="Card image cap" src={props.product.thumbnail} />*!/*/}
                                                    {/*</Col>*/}
                                                    {/*))}*/}
                                                    {/*</Row>*/}
                                                </Col>
                                            </Row>
                                        </CardBody>
                                    ))}
                                </Card>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            }
        </div>
    )
        ;
};

export default OrdersHistory;
