import React, {useState} from "react";
import {
    Button,
    Card,
    CardBody,
    CardTitle,
    Col, Form, FormGroup, Input, Label,
    Row,
} from "reactstrap";
import {useNavigate, useParams} from "react-router-dom";
import 'react-gallery-carousel/dist/index.css';
import numberWithCommas from "../utils/string";
import {useDispatch, useSelector} from "react-redux";
import {userService} from "../services/user.service";
import $ from 'jquery';
import {clearAccessToken, setAccessToken} from "../createReduxStore";
import Loader from "../layouts/loader/Loader";

const Checkout = (props) => {
    const [cartList, setCartList] = useState([]);
    const [isLoading, setIsLoading] = useState([]);
    const user = useSelector((state) => state.user);
    const [fullData, setFullData] = useState(null);
    const navigate = useNavigate();
    const dispatch = useDispatch();

    React.useEffect(() => {
        window.scrollTo(0, 0);
        loadData();
    }, [])

    function updateProductListData(res) {
        setCartList(res.data.items);
    }

    function loadData() {
        setIsLoading(true);
        userService.getCheckoutInfo().then(res => {
            updateProductListData(res);
            setFullData(res.data);
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
            setIsLoading(false);
        });
    }

    function handleCreateOrder(e) {
        var payment = $('input[name="payment"]:checked').val();
        if (!payment) {
            alert("Vui lòng chọn phương thức thanh toán.");
            return;
        } else {
            console.log(payment);
        }
        let params = new URLSearchParams($('#checkoutForm').serialize());
        console.log(params.get('payment'));
        console.log(params.get('name'));
        console.log(params.get('address'));
        console.log(params.get('phone_number'));
        let data = {};
        data['payment'] = params.get('payment');
        data['name'] = params.get('name');
        data['address'] = params.get('address');
        data['phone_number'] = params.get('phone_number');
        console.log(data);
        data['items'] = [];
        cartList.forEach((item) => {
            data['items'].push({
                product: item.product.id,
                count: item.count
            })
        });
        console.log(data);

        if (window.confirm("Bạn có chắn chắn đặt hàng?")) {
            userService.createOrder(data).then(res => {
                alert("Bạn đã đặt hàng thành công!");
                navigate('/orders');
            }).catch(error => {
                console.log(error);
                dispatch(clearAccessToken());
            }).finally(() => {
            });
        } else {
            e.preventDefault();
        }
    }

    return (
        <div>
            {isLoading &&
                <Loader/>
            }
            {!!user && !!fullData &&
                <Form id={'checkoutForm'}>
                    <Row className={'g-2'}>
                        <Col md='8'>
                            <Row>
                                <Col>
                                    {/* --------------------------------------------------------------------------------*/}
                                    {/* Card-1*/}
                                    {/* --------------------------------------------------------------------------------*/}
                                    <Card>
                                        <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                                            <i className="bi bi-check-square me-2"></i>
                                            Kiểm tra lại đơn hàng
                                        </CardTitle>
                                        <CardBody>
                                            <Row className={'text-center border-bottom'}>
                                                <Col md={'2'}>
                                                    Hình ảnh
                                                </Col>
                                                <Col md='4'>
                                                    Tên
                                                </Col>
                                                <Col md='2' className={'d-flex flex-row justify-content-center'}>
                                                    Số lượng
                                                </Col>
                                                <Col md='2'>
                                                    Đơn giá
                                                </Col>
                                                <Col md='2'>
                                                    Tổng giá
                                                </Col>
                                            </Row>
                                            {isLoading &&
                                                <Loader/>
                                            }
                                            {!isLoading && cartList.map((item) => (
                                                <Row key={item.id} className={'text-center mt-2'}>
                                                    <Col md={'2'}>
                                                        <div className="imgWrapper"
                                                             style={{
                                                                 backgroundImage: `url(${item.product.thumbnail})`,
                                                             }}>
                                                            {/*<CardImg className="pt-1" alt="Card image cap" src={props.product.thumbnail} />*/}
                                                        </div>
                                                    </Col>
                                                    <Col md='4'>
                                                        <CardTitle>{item.product.name}</CardTitle>
                                                    </Col>
                                                    <Col md='2' className={'d-flex flex-column'}>
                                                        <div className={'d-flex justify-content-center'}>
                                                            <p className={'mx-2'}>{item.count}</p>
                                                        </div>
                                                    </Col>
                                                    <Col md='2'>
                                                        <CardTitle>{item.product.sale_price}</CardTitle>
                                                    </Col>
                                                    <Col md='2'>
                                                        <CardTitle>{item.product.sale_price * item.count}</CardTitle>
                                                    </Col>
                                                </Row>
                                            ))}
                                        </CardBody>
                                    </Card>
                                </Col>
                            </Row>
                        </Col>
                        <Col md='4'>
                            <Card>
                                <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                                    <i className="bi bi-person-circle me-2"></i>
                                    Thông tin người nhận hàng
                                </CardTitle>
                                <CardBody>
                                    <FormGroup>
                                        <Label for="name">Họ và tên</Label>
                                        <Input
                                            required
                                            id="name"
                                            name="name"
                                            placeholder="Nhập họ và tên"
                                            type="text"
                                            defaultValue={user.name}
                                        />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="address">Địa chỉ</Label>
                                        <Input
                                            required
                                            id="address"
                                            name="address"
                                            placeholder="Nhập địa chỉ"
                                            type="text"
                                            defaultValue={user.address}
                                        />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label for="phone_number">Số điện thoại</Label>
                                        <Input
                                            required
                                            id="phone_number"
                                            name="phone_number"
                                            placeholder="Nhập địa chỉ"
                                            type="tel"
                                            defaultValue={user.phone_number}
                                        />
                                    </FormGroup>
                                </CardBody>
                            </Card>
                            <Card className={'mt-2'}>
                                <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                                    <i className="bi bi-credit-card me-2"></i>
                                    Phương thức thanh toán
                                </CardTitle>
                                <CardBody>
                                    <FormGroup tag="fieldset" required>
                                        {isLoading &&
                                            <Loader/>
                                        }
                                        {!isLoading && !!fullData.payments && fullData.payments.map((item) => (
                                            <FormGroup check key={item.id}>
                                                <Input id={item.id} required name="payment" type="radio"
                                                       value={item.id}/>{" "}
                                                <Label for={item.id.toString()} check>
                                                    {item.name}
                                                </Label>
                                            </FormGroup>
                                        ))}
                                    </FormGroup>
                                </CardBody>
                                <CardBody className="d-flex">
                                    <div className="">Tổng tiền sản phẩm:</div>
                                    <div className="align-right">{numberWithCommas(fullData.sum_price)} VND</div>
                                </CardBody>
                                <CardBody className="d-flex">
                                    <div className="">Phí giao hàng:</div>
                                    <div className="align-right">{numberWithCommas(fullData.shipping_fee)} VND</div>
                                </CardBody>
                                <CardBody className="d-flex">
                                    <div className="">Tổng chi phí:</div>
                                    <div className="align-right font-bold">{numberWithCommas(fullData.total_cost)} VND
                                    </div>
                                </CardBody>
                            </Card>
                            <div className={'d-flex flex-column w-100 mt-2'}>
                                <Button type={'submit'} className={'align-right'} onClick={handleCreateOrder}>Xác nhận
                                    đặt hàng</Button>
                            </div>
                        </Col>
                    </Row>
                </Form>
            }
        </div>
    )
        ;
};

export default Checkout;
