import {
    Card,
    Row,
    Col,
    CardTitle,
    CardBody,
    Button,
    Form,
    FormGroup,
    Label,
    Input,
    FormText,
} from "reactstrap";
import {Link, useNavigate } from "react-router-dom";
import $ from 'jquery';
import {authService} from "../services/public/auth.service";
import Cookies from "js-cookie";
import Constants from "../utils/constants";
import {useDispatch} from 'react-redux';
import {clearAccessToken, setAccessToken, setUser} from "../createReduxStore";
import {userService} from "../services/user.service";

const Forms = () => {
    const dispatch = useDispatch();
    const negative = useNavigate ();

    function loadUser() {
        userService.getUserInfo().then(res => {
            dispatch(setUser({user: res.data}));
            negative('/');
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
            // setIsLoading(false);
        });
    }

    function handleSubmit(event) {
        let params = new URLSearchParams($('#loginForm').serialize());
        authService.login(params).then(res => {
            Cookies.set(Constants.ACCESS_TOKEN, res.data.access_token);
            dispatch(setAccessToken({access_token: res.data.access_token}));
            loadUser();
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error);
            alert(JSON.stringify(error.response.data));
        }).finally(() => {
            // setIsLoading(false);
        });
        event.preventDefault();
    }

    return (
        <Row className={'justify-content-center'}>
            <Col md={'5'}>
                <Card>
                    <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                        <i className="me-2 bi bi-box-arrow-in-right"></i>
                        Đăng nhập
                    </CardTitle>
                    <CardBody>
                        <Form id={'loginForm'} onSubmit={handleSubmit}>
                            <FormGroup>
                                <Label for="username">Username</Label>
                                <Input
                                    id="username"
                                    name="username"
                                    placeholder="Nhập tên đăng nhập"
                                    type="text"
                                />
                            </FormGroup>
                            <FormGroup>
                                <Label for="password">Password</Label>
                                <Input
                                    id="password"
                                    name="password"
                                    placeholder="Nhập mật khẩu"
                                    type="password"
                                />
                            </FormGroup>
                            <Row className={'justify-content-center text-center'}>
                                <Button className={'w-25'}>Xác nhận</Button>
                                <Link to={'/register'} className={'text-hover'}>Chưa có tài khoản? Đăng ký</Link>
                            </Row>
                        </Form>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
};

export default Forms;
