import {
    Card,
    Row,
    Col,
    CardTitle,
    CardBody,
    Button,
    Form,
    FormGroup,
    Label,
    Input,
    FormText,
} from "reactstrap";
import {Link, useNavigate} from "react-router-dom";
import $ from "jquery";
import {authService} from "../services/public/auth.service";

const Forms = () => {
    const negative = useNavigate ();

    function handleSubmit(event) {
        let params = new URLSearchParams($('#registerForm').serialize());
        console.log($('#loginForm').serialize());
        authService.register(params).then(res => {
            alert("Đăng ký tài khoản thành công!");
            negative('/login');
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error.response.data);
            alert(JSON.stringify(error.response.data));
        }).finally(() => {
            // setIsLoading(false);
        });
        event.preventDefault();
    }

    return (
        <Row className={'justify-content-center'}>
            <Col md={'5'}>
                <Card>
                    <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                        <i className="me-2 bi bi-box-arrow-in-left"></i>
                        Đăng ký tài khoản
                    </CardTitle>
                    <CardBody>
                        <Form id={'registerForm'} onSubmit={handleSubmit}>
                            <FormGroup>
                                <Label for="username">Username</Label>
                                <Input
                                    id="username"
                                    name="username"
                                    placeholder="Nhập tên đăng nhập"
                                    type="text"
                                />
                            </FormGroup>
                            <FormGroup>
                                <Label for="password">Password</Label>
                                <Input
                                    id="password"
                                    name="password"
                                    placeholder="Nhập mật khẩu"
                                    type="password"
                                />
                            </FormGroup>
                            <FormGroup>
                                <Label for="email">Email</Label>
                                <Input
                                    id="email"
                                    name="email"
                                    placeholder="Nhập email"
                                    type="email"
                                />
                            </FormGroup>
                            <Row className={'justify-content-center text-center'}>
                                <Button className={'w-25'}>Xác nhận</Button>
                                <Link to={'/login'} className={'text-hover'}>Đã có tài khoản? Đăng nhập</Link>
                            </Row>
                        </Form>
                    </CardBody>
                </Card>
            </Col>
        </Row>
    );
};

export default Forms;
