import React from "react"
import ContentLoader from "react-content-loader"

const ProductListSke = (props) => (
    <ContentLoader
        speed={2}
        width={230}
        height={500}
        viewBox="0 0 230 500"
        backgroundColor="#dedede"
        foregroundColor="#d6d6d6"
        {...props}
    >
        <rect x="20" y="290" rx="2" ry="2" width="140" height="10" />
        <rect x="0" y="0" rx="2" ry="2" width="380" height="250" />
        <rect x="17" y="266" rx="0" ry="0" width="198" height="10" />
        <rect x="19" y="328" rx="0" ry="0" width="199" height="7" />
        <rect x="20" y="348" rx="0" ry="0" width="198" height="8" />
        <rect x="20" y="369" rx="0" ry="0" width="187" height="4" />
        <rect x="22" y="390" rx="0" ry="0" width="173" height="6" />
        <rect x="15" y="444" rx="0" ry="0" width="108" height="27" />
    </ContentLoader>
)

export default ProductListSke

