import {ListGroup, ListGroupItem} from "reactstrap";
import React from "react";

import {Link, useNavigate} from "react-router-dom";

const MySideBar = (props) => {
    const navigate = useNavigate();

    return (
        <div>
            <ListGroup>
                <Link to={'/profile'}>
                    <ListGroupItem
                        action
                        disabled={props.activePage == 'userProfile'}
                        href="#"
                        tag="a"
                    >
                        <i className="bi bi-person-bounding-box me-2"></i>Thông tin cá nhân
                    </ListGroupItem>
                </Link>
                <Link to={'/orders'}>
                    <ListGroupItem
                        action
                        disabled={props.activePage == 'ordersHistory'}
                        href="#"
                        tag="a"
                    >
                        <i className="bi bi-clock-history me-2"></i>Lịch sử đơn hàng
                    </ListGroupItem>
                </Link>
                <Link to={'/cart'}>
                    <ListGroupItem
                        action
                        disabled={props.activePage == 'cart'}
                        href="#"
                        tag="a"
                    >
                        <i className="bi bi-cart4 me-2"></i>Giỏ hàng
                    </ListGroupItem>
                </Link>
            </ListGroup>

        </div>
    );
};

export default MySideBar;
