import axios from 'axios';
import Constants from "../utils/constants";
import getHeader from "../utils/auth";

export const userService = {
    getUserInfo,
    getAllOrder,
    getNextPage,
    getAllCart,
    addCart,
    subCart,
    deleteCartItem,
    getCheckoutInfo,
    createOrder,
    updateInfo,
};


function getUserInfo() {
    console.log(getHeader());
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/user/me`,
        headers: getHeader(),
    });
}

function register(data) {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/brands`,
        data: data
    });
}

function createOrder(data) {
    return axios({
        method: 'POST',
        url: `${Constants.SERVER}/user/orders`,
        data: data,
        headers: getHeader()
    });
}

function updateInfo(data) {
    return axios({
        method: 'PUT',
        url: `${Constants.SERVER}/user/me`,
        data: data,
        headers: getHeader()
    });
}

function getAllOrder() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/user/orders?page_size=10`,
        headers: getHeader(),
    });
}

function getAllCart() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/user/carts`,
        headers: getHeader(),
    });
}

function getCheckoutInfo() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/user/carts?detail=1`,
        headers: getHeader(),
    });
}


function deleteCartItem(cartItemId) {
    return axios({
        method: 'DELETE',
        url: `${Constants.SERVER}/user/carts/${cartItemId}`,
        headers: getHeader(),
    });
}

function addCart(productId) {
    let data = {
        'product': productId,
    }
    return axios({
        method: 'PUT',
        url: `${Constants.SERVER}/user/carts/add`,
        headers: getHeader(),
        data: data
    });
}

function subCart(productId) {
    let data = {
        'product': productId,
    }
    return axios({
        method: 'PUT',
        url: `${Constants.SERVER}/user/carts/remove`,
        headers: getHeader(),
        data: data
    });
}

function getNextPage(nextPage) {
    return axios({
        method: 'GET',
        url: nextPage,
    });
}