import axios from 'axios';
import Constants from "../../utils/constants";

export const publicCategoryService = {
    getAllCategory,
    getAllBrand,
};


function getAllCategory() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/categories`
    });
}

function getAllBrand() {
    return axios({
        method: 'GET',
        url: `${Constants.SERVER}/brands`
    });
}