import {
    Card,
    CardBody,
    CardSubtitle,
    CardTitle,
    Row, Col, CardGroup,
} from "reactstrap";
import numberWithCommas from "../utils/string";
import {Link} from "react-router-dom";
import { Rating } from 'react-simple-star-rating'

const ProductCard = (props) => {
    return (
        <Card className="mb-1 product-card hover-zoom">
            <Link to={"/products/" + props.product.id}>
                <div className="imgWrapper"
                     style={{
                         backgroundImage: `url(${props.product.thumbnail})`,
                     }}>
                </div>
                <CardBody className="pt-2 p-4">
                    <CardTitle className="title" tag="h5">{props.product.name}</CardTitle>
                    <Row>
                        <CardGroup>
                            <Col><CardSubtitle
                                className="subtitle2">{numberWithCommas(props.product.price)} VND</CardSubtitle></Col>
                            <Col><CardSubtitle
                                className="subtitle">{numberWithCommas(props.product.sale_price)} VND</CardSubtitle></Col>
                        </CardGroup>
                    </Row>
                    <Rating readonly size={25} ratingValue={parseFloat(props.product.avg_rating) /5 * 100}/>
                </CardBody>
            </Link>
        </Card>
    );
};

export default ProductCard;
