import {
    Card,
    CardTitle,
     Row, Col,
} from "reactstrap";
import CategoryCard from "./Category";
import {Link, useLocation} from "react-router-dom";
import React, {useState} from "react";
import {publicCategoryService} from "../services/public";
import category from "./Category";

const priceListRaw = [
    {
        price_lowest: 0,
        price_highest: 5000000,
        text: "Dưới 5 triệu"
    },
    {
        price_lowest: 5000000,
        price_highest: 10000000,
        text: "Từ 5 đến 10 triệu"
    },
    {
        price_lowest: 10000000,
        price_highest: 15000000,
        text: "Từ 10 đến 15 triệu"
    },
    {
        price_lowest: 15000000,
        price_highest: 20000000,
        text: "Từ 15 đến 20 triệu"
    },
    {
        price_lowest: 20000000,
        price_highest: 999999999,
        text: "Trên 20 triệu"
    }
];

const ProductFilter = (props) => {
    const location = useLocation();
    const { search } = useLocation();
    const [categoryData, setCategoryData] = useState([]);
    const [activeCategory, setActiveCategory] = useState(-1);
    const [activeBrand, setActiveBrand] = useState(-1);
    const [activePrice, setActivePrice] = useState(-1);
    const [brands, setBrands] = useState([]);
    const [priceList, setPriceList] = useState([]);

    function loadBrandList() {
        publicCategoryService.getAllBrand().then(res => {
            setBrands(res.data);
        }).catch(error => {
            console.log(error)
        }).finally(() => {
        });
    }

    React.useEffect(() => {
        if (categoryData.length == 0) return;
        console.log("activeeee", activeCategory.toString());
        if (!!activeCategory && activeCategory !== -1) {
            console.log("refetchhhh", props.data.length)
            categoryData.map((item) => {
                console.log(item.id, activeCategory);
                if (item.id === activeCategory) {
                    console.log("refetchhhh",item.brands);
                    setBrands(item.brands);
                }
            });
        }
        else loadBrandList();
        setPriceList(priceListRaw);
    }, [activeCategory, categoryData]);

    React.useEffect(async () => {
        let query = new URLSearchParams(search);
        setActiveCategory(parseInt(query.get('category')));
        setActiveBrand(parseInt(query.get('brand')));
        setActivePrice(parseInt(query.get('price_lowest')));
    }, [location]);

    React.useEffect(() => {
        publicCategoryService.getAllCategory().then(res => {
            setCategoryData(res.data);
        }).catch(error => {
            console.log(error);
        }).finally(() => {
        });
    }, []);

    function getSearchUrlCategory(categoryId) {
        let query = new URLSearchParams(search);
        if (categoryId == activeCategory) {
            query.delete('category');
        } else {
            query.set('category', categoryId);
        }
        query.delete('brand');
        return query.toString();
    }

    function getSearchUrlBrand(brandId) {
        let query = new URLSearchParams(search);
        if (brandId == activeBrand) {
            query.delete('brand');
        } else {
            query.set('brand', brandId);
        }
        return query.toString();
    }

    function getSearchUrlPrice(item) {
        let query = new URLSearchParams(search);
        if (item.price_lowest == activePrice) {
            query.delete('price_lowest');
            query.delete('price_highest');
        } else {
            query.set('price_lowest', item.price_lowest);
            query.set('price_highest', item.price_highest);
        }
        return query.toString();
    }

    return (
        <Card >
            <CardTitle className="pt-3 ps-3 font-bold" tag="h5">BỘ LỌC</CardTitle>
            <Row className="p-4 g-2">
                <Col sm="3" lg="3" xl="3">Danh mục:</Col>
                <Col sm="9" lg="9" xl="9" className="d-flex flex-row flex-wrap">
                    {categoryData.map((item) => (
                        <Link key={item.id} to={{pathname:"/products", search: getSearchUrlCategory(item.id)}}>
                            <div key={item.id} className="me-2 mb-2"><CategoryCard text={item.name} active={item.id===activeCategory}/></div>
                        </Link>
                    ))}
                </Col>

                <Col sm="3" lg="3" xl="3">Thương hiệu:</Col>
                <Col sm="9" lg="9" xl="9" className="d-flex flex-row flex-wrap">
                    {brands.map((item) => (
                        <Link key={item.id} to={{pathname:"/products", search: getSearchUrlBrand(item.id)}}>
                            <div key={item.id} className="me-2 mb-2"><CategoryCard text={item.name} active={item.id===activeBrand}/></div>
                        </Link>
                    ))}
                </Col>

                <Col sm="3" lg="3" xl="3">Giá cả:</Col>
                <Col sm="9" lg="9" xl="9" className="d-flex flex-row">
                    {priceList.map((item) => (
                        <Link key={item.price_lowest} to={{pathname:"/products", search: getSearchUrlPrice(item)}}>
                            <div key={item.price_lowest} className="me-2 mb-2"><CategoryCard text={item.text} active={item.price_lowest===activePrice}/></div>
                        </Link>
                    ))}
                </Col>
            </Row>
        </Card>
    );
};

export default ProductFilter;
