import React, {useEffect, useState} from "react";
import {publicCategoryService, publicProductService} from "../services/public";
import {Button, Card, CardBody, CardGroup, CardSubtitle, CardTitle, Col, Row, Table} from "reactstrap";
import ProductCard from "../components/ProductCard";
import Skeleton, {SkeletonTheme} from "react-loading-skeleton";
import ProductListSke from "../components/skeletons/ProductListSke";
import {Link, useLocation, useParams} from "react-router-dom";
import ProductFilter from "../components/ProductFilter";
import Carousel from "react-gallery-carousel";
import 'react-gallery-carousel/dist/index.css';
import numberWithCommas from "../utils/string";
import CategoryCard from "../components/Category";
import styled, {css} from "styled-components";
import {ellipsis} from "polished";
import {Rating} from "react-simple-star-rating";
import {userService} from "../services/user.service";
import {clearAccessToken} from "../createReduxStore";
import {useDispatch} from "react-redux";
import Loader from "../layouts/loader/Loader";

const DescriptionText = styled.div`
  ${({showMore}) =>
          showMore &&
          css`
            display: -webkit-box;
            max-width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: normal;
            word-wrap: normal;
            -webkit-box-orient: vertical;
            //height: 600px;
          `}
`;

const ShowMoreText = styled.div`
  cursor: pointer;
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 30px;
  text-decoration: underline;
`;

const ProductList = (props) => {
    const [isShowMore, setIsShowMore] = useState(true);
    const toggleReadMore = () => setIsShowMore((show) => !show);
    const [productData, setProductData] = useState(null);
    const [images, setImages] = useState([]);
    const [productList, setProductList] = useState([]);
    const [isLoading, setIsLoading] = useState([]);
    const {id} = useParams();
    const dispatch = useDispatch();

    const location = useLocation()
    const {search} = useLocation();
    let query = new URLSearchParams(search);

    function numberWithCommas(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    React.useEffect(() => {
        console.log("router change detect");
        console.log(productList);
        setProductList([]);
        loadData(id);
    }, [location]);

    React.useEffect(() => {
        window.scrollTo(0, 0);
    }, [])

    function updateProductListData(res) {
        console.log(res.data);
        setProductData(res.data);
        let imageList = [];
        res.data.images.forEach(function (item) {
            imageList.push({
                src: item.url
            })
        });
        setImages(imageList);
    }

    function loadData(id) {
        setIsLoading(true);
        publicProductService.getProductDetail(id).then(res => {
            updateProductListData(res);
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error)
        }).finally(() => {
            setIsLoading(false);
        });
    }

    function handleAddCart() {
        userService.addCart(productData.id).then(res => {
            alert("Đã cập nhật giỏ hàng của bạn.");
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
        })
    }

    return (
        <div>
            {!productData &&
                <Loader/>
            }
            {productData &&
                <div>
                    <Card>
                        <Row className={'p-4'}>
                            <Col md="6">
                                <Carousel objectFit={'cover'} images={images} hasMediaButton={false}
                                          style={{height: '100%', width: '100%', backgroundColor: 'white'}}/>
                            </Col>
                            <Col md="6">
                                <CardBody>
                                    <h1 className="title font-bold" tag="h5">{productData.name}</h1>
                                    <CardSubtitle>Thương hiệu: {productData.brand.name} |
                                        ID: {productData.id}</CardSubtitle>
                                    <Rating readonly size={25}
                                            ratingValue={parseFloat(productData.avg_rating) / 5 * 100}/>
                                    <h3 className='m-0 text-danger'>{numberWithCommas(productData.sale_price)} VND</h3>
                                    <p className={'text-decoration-line-through'}>{numberWithCommas(productData.price)} VND</p>
                                    <Row className='g-1'>
                                        {/*<Col md='6'>*/}
                                        {/*    <Button className={'w-100'}>Mua Ngay</Button></Col>*/}
                                        <Col md='6'>
                                            <Button className={'w-100'} outline onClick={handleAddCart}>Thêm vào giỏ hàng</Button></Col></Row>
                                </CardBody>

                            </Col>
                        </Row>
                    </Card>
                    <Card className={'mt-4'}>
                        <Row className={'g-0'}>
                            <Col md='8'>
                                <CardTitle className="pt-3 ps-3 font-bold" tag="h5">Mô tả sản phẩm</CardTitle>
                                <CardBody className='pe-0 overflow-hidden'>
                                    <div class={'description'} dangerouslySetInnerHTML={{__html: productData.description}}/>
                                    {/*<DescriptionText className={'description'} showMore={isShowMore}*/}
                                    {/*                 dangerouslySetInnerHTML={{__html: productData.description}}></DescriptionText>*/}
                                    {/*<ShowMoreText onClick={toggleReadMore}>*/}
                                    {/*    {isShowMore ? "Show more..." : "Show less"}*/}
                                    {/*</ShowMoreText>*/}
                                </CardBody>
                            </Col>
                            <Col md='4'>
                                <CardTitle className="pt-3 ps-3 font-bold" tag="h5">Thông tin chi tiết</CardTitle>
                                <CardBody>
                                    <Table
                                        bordered
                                        responsive
                                        size=""
                                        striped
                                    >
                                        <tbody>
                                        {productData.specifications.map((item, index) => (
                                            item.value.length > 0 ?
                                                <tr key={index}>
                                                    <td>
                                                        {item.name}
                                                    </td>
                                                    <td>
                                                        {item.value}
                                                    </td>
                                                </tr> :
                                                <tr key={index}>
                                                    <td className={'font-bold'} colSpan="2">
                                                        {item.name}
                                                    </td>
                                                </tr>
                                        ))}
                                        </tbody>
                                    </Table>
                                </CardBody>
                            </Col>
                        </Row>
                    </Card>
                </div>
            }
        </div>
    )
        ;
};

export default ProductList;
