import React, {useState} from "react";
import {
    Button,
    Card,
    CardBody,
    CardTitle,
    Col,
    Row,
    Table
} from "reactstrap";
import {Link} from "react-router-dom";
import 'react-gallery-carousel/dist/index.css';
import styled, {css} from "styled-components";
import {useDispatch, useSelector} from "react-redux";
import MySideBar from "../components/MySideBar";
import {userService} from "../services/user.service";
import {clearAccessToken} from "../createReduxStore";
import Loader from "../layouts/loader/Loader";

const DescriptionText = styled.div`
  ${({showMore}) =>
          showMore &&
          css`
            display: -webkit-box;
            max-width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: normal;
            word-wrap: normal;
            -webkit-box-orient: vertical;
            height: 600px;
          `}
`;

const ShowMoreText = styled.div`
  cursor: pointer;
  font-size: 14px;
  margin-top: 20px;
  margin-bottom: 30px;
  text-decoration: underline;
`;

const Cart = (props) => {
    const [cartList, setCartList] = useState([]);
    const [isLoading, setIsLoading] = useState([]);
    const [nextPage, setNextPage] = useState([]);
    const user = useSelector((state) => state.user);
    const dispatch = useDispatch();

    React.useEffect(() => {
        window.scrollTo(0, 0);
        loadData();
    }, [])

    function updateProductListData(res) {
        setCartList(res.data);
    }

    function loadData() {
        setIsLoading(true);
        userService.getAllCart().then(res => {
            console.log(res);
            updateProductListData(res);
            // setApiResponse(res.data);
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
            setIsLoading(false);
        });
    }

    function handleAddItem(productId) {
        userService.addCart(productId).then(res => {
            loadData();
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
        })
    }

    function handleSubItem(productId) {
        userService.subCart(productId).then(res => {
            loadData();
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
        })
    }

    function handleDeleteItem(productId) {
        userService.deleteCartItem(productId).then(res => {
            loadData();
        }).catch(error => {
            console.log(error);
            dispatch(clearAccessToken());
        }).finally(() => {
        })
    }


    return (
        <div>
            {!!user &&
                <Row className={'g-2'}>
                    <Col md='4'>
                        <MySideBar activePage={'cart'}/>
                    </Col>
                    <Col md='8'>
                        <Row>
                            <Col>
                                {/* --------------------------------------------------------------------------------*/}
                                {/* Card-1*/}
                                {/* --------------------------------------------------------------------------------*/}
                                <Card>
                                    <CardTitle tag="h6" className="border-bottom p-3 mb-0">
                                        <i className="bi bi-cart4 me-2"></i>
                                        Giỏ hàng của bạn
                                    </CardTitle>
                                    <CardBody>
                                        <Row className={'text-center border-bottom'}>
                                            <Col md={'2'}>
                                                Hình ảnh
                                            </Col>
                                            <Col md='4'>
                                                Tên
                                            </Col>
                                            <Col md='2' className={'d-flex flex-row justify-content-center'}>
                                                Số lượng
                                            </Col>
                                            <Col md='2'>
                                                Đơn giá
                                            </Col>
                                            <Col md='2'>
                                                Tổng giá
                                            </Col>
                                        </Row>
                                        {isLoading &&
                                            <Loader/>
                                        }
                                        {!isLoading && cartList.length === 0 &&
                                            <p className={'text-center'}>Giỏ hàng của bạn đang trống, nhấn <Link className={'text-hover text-danger'} to={'/'}>vào đây</Link> để xem sản phẩm.</p>
                                        }
                                        {!isLoading && cartList.map((item) => (
                                            <Row key={item.id} className={'text-center mt-2'}>
                                                <Col md={'2'}>
                                                    <div className="imgWrapper"
                                                         style={{
                                                             backgroundImage: `url(${item.product.thumbnail})`,
                                                         }}>
                                                        {/*<CardImg className="pt-1" alt="Card image cap" src={props.product.thumbnail} />*/}
                                                    </div>
                                                </Col>
                                                <Col md='4'>
                                                    <CardTitle>{item.product.name}</CardTitle>
                                                </Col>
                                                <Col md='2' className={'d-flex flex-column'}>
                                                    <div className={'d-flex justify-content-center'}>
                                                        <div className={'text-hover'}
                                                             onClick={() => handleSubItem(item.product.id)}><i
                                                            className="bi bi-arrow-left"></i></div>
                                                        <p className={'mx-2'}>{item.count}</p>
                                                        <div className={'text-hover'}
                                                             onClick={() => handleAddItem(item.product.id)}><i
                                                            className="bi bi-arrow-right"></i></div>
                                                    </div>
                                                    <div className={'text-hover'}
                                                         onClick={() => handleDeleteItem(item.id)}><p
                                                        className={'text-danger'}>Xóa</p></div>
                                                </Col>
                                                <Col md='2'>
                                                    <CardTitle>{item.product.sale_price}</CardTitle>
                                                </Col>
                                                <Col md='2'>
                                                    <CardTitle>{item.product.sale_price * item.count}</CardTitle>
                                                </Col>
                                            </Row>
                                        ))}
                                    </CardBody>
                                </Card>
                                {!isLoading && cartList.length > 0 &&
                                    <div className={'d-flex flex-column w-100 mt-2'}>
                                        <Link to={'/checkout'} className={'align-right'}>
                                            <Button className={'align-right'}>Đến trang thanh toán</Button>
                                        </Link>
                                    </div>
                                }
                            </Col>
                        </Row>
                    </Col>
                </Row>
            }
        </div>
    )
        ;
};

export default Cart;
