import axios from 'axios';
import Constants from "../../utils/constants";

export const authService = {
    login,
    register,
};


function login(data) {
    return axios({
        method: 'POST',
        url: `${Constants.SERVER}/login`,
        data: data
    });
}

function register(data) {
    return axios({
        method: 'POST',
        url: `${Constants.SERVER}/register`,
        data: data
    });
}
